body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Note {
    width: 98%;
    border: 1px solid grey;
    margin: 0 0 2px 0;
    cursor: pointer
}
.AddNote {
    width: 98%;
    border: 1px solid grey;
    margin: 0 0 2px 0;
    padding-top: 1rem;
    cursor: pointer;
    min-height: 80px;
}
.AddNote__Title {
    width: 78%;
    padding: 0.3rem;
    border: 1px solid blue;
    margin-bottom: 1rem;
}
.AddNote__Content {
    width: 78%;
    padding: 0.3rem;
    border: 1px solid blue;
    margin-bottom: 1rem;
}
.AddNote__Save {
    display: block;
    margin: 0 auto 1rem auto;
}
.App {
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.active {
  box-shadow: 2px 2px 2px #000;
}
.red {
  color: red;
}

.bold {
  font-weight: bold;
}
